import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import {
  validateEmail,
  validatePassword,
  validateConfirmPassword
} from '../../../utils/validation';
import {
  register,
  setEmail,
  setPassword,
  setConfirmPassword,
  setEmailValid,
  setPasswordValid,
  setConfirmPasswordValid,
  setEmailError,
  setPasswordError,
  setConfirmPasswordError,
  setRegisterError,
  setRegisterSuccess,
  resetForm
} from './registerSlice';
import {
  RegisterContainer,
  RegisterTitle,
  RegisterBox,
  Label,
  Input,
  Button,
  HelperText,
  Link,
  SuccessText,
  ErrorText
} from './style.js';

const Register = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const {
    email,
    password,
    confirmPassword,
    emailValid,
    passwordValid,
    confirmPasswordValid,
    emailError,
    passwordError,
    confirmPasswordError,
    registerError,
    registerSuccess
  } = useSelector((state) => state.register);

  const handleEmailChange = (e) => {
    const inputEmail = e.target.value;
    dispatch(setEmail(inputEmail));
    const isValid = validateEmail(inputEmail);
    dispatch(setEmailValid(isValid));
    dispatch(setEmailError(isValid ? '' : ''));
  };

  const handlePasswordChange = (e) => {
    const inputPassword = e.target.value;
    dispatch(setPassword(inputPassword));
    const isValid = validatePassword(inputPassword);
    dispatch(setPasswordValid(isValid));
    dispatch(setPasswordError(isValid ? '' : ''));
  };

  const handleConfirmPasswordChange = (e) => {
    const inputConfirmPassword = e.target.value;
    dispatch(setConfirmPassword(inputConfirmPassword));
    const isValid = validateConfirmPassword(password, inputConfirmPassword);
    dispatch(setConfirmPasswordValid(isValid));
    dispatch(setConfirmPasswordError(isValid ? '' : ''));
  };

  const handleRegister = async (e) => {
    e.preventDefault();
    if (!emailValid || !passwordValid || !confirmPasswordValid) {
      dispatch(setRegisterError(''));
      return;
    }

    try {
      await dispatch(register({ email, password })).unwrap();
      dispatch(setRegisterSuccess(''));
      setTimeout(() => {
        navigate('/');
        dispatch(resetForm());
      }, 500);
    } catch (error) {
      dispatch(setRegisterError(''));
      setTimeout(() => dispatch(setRegisterError('')), 3000);
    }
  };

  useEffect(() => {
    return () => {
      dispatch(resetForm());
    };
  }, [dispatch]);

  return (
    <RegisterContainer>
      <RegisterTitle></RegisterTitle>
      <RegisterBox>
        <form onSubmit={handleRegister} noValidate>
          <Label>:</Label>
          <Input
            type="email"
            placeholder="Email"
            value={email}
            maxLength={50}
            onChange={handleEmailChange}
          />
          {email && (emailValid ? <SuccessText></SuccessText> : <ErrorText> {emailError}</ErrorText>)}

          <Label>:</Label>
          <Input
            type="password"
            placeholder="Password"
            value={password}
            maxLength={20}
            onChange={handlePasswordChange}
          />
          {password && (passwordValid ? <SuccessText></SuccessText> : <ErrorText> {passwordError}</ErrorText>)}

          <Label>:</Label>
          <Input
            type="password"
            placeholder="Password"
            value={confirmPassword}
            maxLength={20}
            onChange={handleConfirmPasswordChange}
          />
          {confirmPassword && (confirmPasswordValid ? <SuccessText></SuccessText> : <ErrorText> {confirmPasswordError}</ErrorText>)}

          <Button type="submit"></Button>
          {registerError && <ErrorText>{registerError}</ErrorText>}
          {registerSuccess && <SuccessText>{registerSuccess}</SuccessText>}

          <HelperText>
            <Link onClick={() => navigate('/Login')}></Link>
          </HelperText>
        </form>
      </RegisterBox>
    </RegisterContainer>
  );
};

export default Register;