import React, { useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { 
  validateEmail,
  validatePassword
} from '../../../utils/validation';
import { 
  setEmail, 
  setPassword, 
  setEmailValid, 
  setPasswordValid, 
  setEmailError, 
  setPasswordError, 
  setLoginError, 
  resetForm, 
  login
} from './loginSlice';
import { 
  LoginContainer,
  LoginTitle,
  LoginBox,
  Label,
  Input,
  Button,
  HelperText,
  Link,
  SuccessText,
  ErrorText
} from './style.js';

const Login = () => {
  const dispatch = useDispatch(); 
  const navigate = useNavigate();
  const location = useLocation();
  const { from } = location.state || { from: { pathname: "/" } };

  const {
    email,
    password,
    emailValid,
    passwordValid,
    emailError,
    passwordError,
    error,
    success,
    loading,
    isAuthenticated
  } = useSelector((state) => state.login);

  const handleEmailChange = (e) => {
    const inputEmail = e.target.value;
    dispatch(setEmail(inputEmail));
    const isValid = validateEmail(inputEmail);
    dispatch(setEmailValid(isValid));
    dispatch(setEmailError(isValid ? '' : ''));
  };

  const handlePasswordChange = (e) => {
    const inputPassword = e.target.value;
    dispatch(setPassword(inputPassword));
    const isValid = validatePassword(inputPassword);
    dispatch(setPasswordValid(isValid));
    dispatch(setPasswordError(isValid ? '' : ''));
  };

  const handleLogin = async (e) => {
    e.preventDefault();

    if (!emailValid || !passwordValid) {
      dispatch(setLoginError(''));
      return;
    }

    try {
      await dispatch(login({ email, password })).unwrap();
      dispatch(resetForm());
      navigate(from);
    } catch (errorMessage) {
      dispatch(setLoginError(''));
      setTimeout(() => {
        dispatch(setLoginError(''));
        dispatch(resetForm());
      }, 3000);
    }
  };

  //  handleLogin 
  useEffect(() => {
    if (isAuthenticated) {
      navigate(from);
    }
  }, [isAuthenticated, navigate, from]);

    // 
    useEffect(() => {
      return () => {
        dispatch(resetForm());
      };
    }, [dispatch]);

  return (
    <LoginContainer>
      <LoginTitle></LoginTitle> 
      <LoginBox>
        <form onSubmit={handleLogin} noValidate>
          <Label>:</Label>
          <Input 
            type="email" 
            placeholder="Email" 
            value={email} 
            maxLength={50}
            onChange={handleEmailChange} 
            required 
          />
          {email && (emailValid ? <SuccessText></SuccessText> : <ErrorText> {emailError}</ErrorText>)}
          <Label>:</Label>
          <Input 
            type="password" 
            placeholder="Password" 
            value={password}
            maxLength={20}
            onChange={handlePasswordChange} 
            required 
          />
          {password && (passwordValid ? <SuccessText></SuccessText> : <ErrorText> {passwordError}</ErrorText>)}
          <Button type="submit" disabled={loading}></Button>
          {error && <ErrorText>{error}</ErrorText>}
          {success && <SuccessText>{success}</SuccessText>}
          <HelperText>
            <Link onClick={() => navigate('/forgotpassword/email')}>?</Link> / <Link onClick={() => navigate('/Register')}></Link>
          </HelperText>
        </form>
      </LoginBox>
    </LoginContainer>
  );
};

export default Login;